@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&family=Montserrat&display=swap");

.App {
    min-height: 100vh;
    height: auto;
    text-align: center;
    font-family: "Montserrat", sans-serif;
    background: linear-gradient(135deg, #e0f7fa, #ffebee); /* Soft gradient background */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1); /* Subtle shadow effect */
}

h1 {
    font-family: "Fredoka One", cursive;
    color: #2e0d7c;
    font-size: 48px;
    margin-bottom: 10px;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1); /* Text shadow for depth */
}

pre {
    font-size: 24px;
    color: #113a5d;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Box shadow for a floating effect */
    margin-bottom: 20px;
}

hr {
    margin-top: 20px;
    width: 60%;
    border: 1px solid #ff7a8a;
}

.inp {
    width: 220px;
    height: 45px;
    border: 2px solid #113a5d;
    margin: 15px 10px; /* Added vertical margin */
    padding-left: 6px;
    font-size: 22px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Shadow for input fields */
    transition: all 0.3s ease;
}

.inp:focus {
    border-color: #24022c;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Increased shadow on focus */
    outline: none;
}

.btn {
    width: 100px;
    height: 45px;
    background-color: #2e0d7c;
    border: none;
    color: #fff;
    font-size: 18px;
    border-radius: 5px;
    margin-top: 20px; /* Increased margin above the button */
    margin-bottom: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Shadow effect for buttons */
}

.btn:hover {
    background-color: #110235;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Hover effect with deeper shadow */
}
